.header{
    position: fixed;
    width: 90%;
    /* margin: 2rem; */
    border: 2px solid rgba(255,255,255,.6);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    margin: 0px auto;
    margin-top: 3rem;
  }

.header h1{
    margin: 0px;
}

.arrow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding-left: 20px;
}

.arrow img{
    width: 20px;
    height: 20px;
}

.arrow img:hover{
    border-bottom: 1px solid white; 
}


